<template>
  <div class="container" id="guesshome">
    <header class="header">
      <div class="top-txt"></div>
      <div class="top-title"></div>
    </header>
    <section class="guess-mainbody">
        <div class="guess-func">
          <a v-link="{path: '/guess-rule'}"></a>
          <a v-link="{path: '/guess-bang'}">bang</a>
          <a :class="{'active':musicOpen}" @click="bgMusic">music</a>
          <div class="guess-score">
            40002
            <span class="add-score" :class="{'active': addScoreToggle}" @click="addScore"></span>
          </div>
        </div>
        <div class="guess-main">
            <div class="gs-result" v-if="gsResult">
                <span v-text="resultNum.a"></span>,
                <span v-text="resultNum.b"></span>,
                <span v-text="resultNum.c"></span>
                <span class="resultcount" v-text="resultNum.count ? '': ''"></span>
                <div v-if="resultNum.count">
                    <br>2222
                </div>
                <div v-else>
                   <br>
                </div>
            </div>
            <div class="gs-roller" :class="{'active': rock.flag}" transiton="aa">
              <div class="gs-roller-cent"></div>
              <div class="gs-roller-bot">
                <span class="sanzi" :class="sanziGroup.one"></span>
                <span class="sanzi" :class="sanziGroup.two"></span>
                <span class="sanzi" :class="sanziGroup.three"></span>
              </div>
            </div>


  
          <div class="guess-raise">2</div>
        </div>
        <div class="guess-call">
            <a class="gu-call-small" :class="{'active': gscallStatus == 'small'}" @click="gsCall('small')"></a>
            <a class="gu-call-baozi" :class="{'active': gscallStatus == 'baozi'}" @click="gsCall('baozi')"></a>
            <a class="gu-call-big" :class="{'active': gscallStatus == 'big'}" @click="gsCall('big')"></a>
        </div>
    </section>
  </div>
<!--   <audio autoplay="autoplay" style="display: none; position: absolute; width: 0px; height: 0px; left: 0px; bottom: 0px;">
      <source type="audio/mpeg" src="http://7tsy8v.com1.z0.glb.clouddn.com/bgmusic.mp3">
  </audio> -->
</template>

<script>

export default {
  data () {
    let _self = this;
    return {
      msg: 'bang',
      addScoreToggle: false,
      musicOpen: true,
      gscallStatus: '',
      gsResult: false,
      resultNum:{
        a:'',
        b:'',
        c:'',
        count: ''
      },
      sanziGroup: {
        one: 'one',
        two: 'two',
        three: 'three',
      },
      rock: {
        flag: false,
        hideFn:function(){
            setTimeout(function () {
                _self.rock.flag = false;
                _self.gsResult = true;
                _self.gscallStatus = ''
            }, 2200);
        }
      }
    }
  },
  methods:{
      addScore (){
        let _self = this;
        _self.addScoreToggle = true;
        console.log('ss');
      },
      bgMusic (){
        let _self = this;
        _self.musicOpen = !_self.musicOpen;
      },
      selectfrom (lowValue,highValue){

        let _self = this;
        let a,b,c;
        let choice=highValue-lowValue+1;
        let zimu = ['one','two','three','four','five','six'];
         _self.gsResult = false;
        a = Math.floor(Math.random()*choice+lowValue);
        b = Math.floor(Math.random()*choice+lowValue);
        c = Math.floor(Math.random()*choice+lowValue);
        _self.resultNum.a = a;
        _self.resultNum.b = b;
        _self.resultNum.c = c;
        _self.resultNum.count = (a+b+c) >10 ? true:false ;

        setTimeout(function(){
           _self.sanziGroup.one = zimu[a - 1];
        _self.sanziGroup.two = zimu[b - 1];
        _self.sanziGroup.three = zimu[c - 1];
        console.log(a+b+c+'');

      },300)
       
        _self.rock.flag = true;
            _self.rock.hideFn();
      },
      gsCall (option){
        let _self = this;
        switch(option){
          case 'small':
          if(!_self.rock.flag){
            _self.selectfrom(1,6);
            console.log('');
             _self.gscallStatus = 'small';
             
          }         
           break;


          case 'baozi':
          if(!_self.rock.flag){
            _self.selectfrom(1,6);
            console.log('');
             _self.gscallStatus = 'baozi';
              
           
          }         
           break;


          default:
          if(!_self.rock.flag){
            _self.selectfrom(1,6);
            console.log('');
             _self.gscallStatus = 'big';
           
          }         
          
        }
        if(option == 'small'){

        }
      }
  }
}
</script>

