}
  .gs-roller{
    @include size(370,700);
    margin: 0 auto;
    position:relative;
       -webkit-transition:all 0.2s 0.3s linear;
       transition:all 0.2s 0.3s linear;

    &.active{ 
         
          animation: rocks 2s linear 0.6s infinite;
