      @extend %sanzi-num;
      position:absolute;
      &:first-child{
          top:rem(20);
          left: rem(50)
      }
      &:nth-child(2){
          top:rem(50);
          left: rem(140);
      }
      &:nth-child(3){
        right:rem(50);
