export default function(router){
	router.map({
		'/':{
			name: 'home',
			component:require('./App.vue')
		},
		'/guess':{
			name: 'guesshome',
			component:require('./views/guessHome.vue')
		},
		'/guess-rule':{
			name: 'guessrule',
			component:require('./views/guessRule.vue')
		},
		'/guess-bang':{
			name: 'guessbang',
			component:require('./views/guessBang.vue')
		}
	})
}


